export default {
	// 当页面加载时触发
	onLoad() {
		// 获取编辑个人数据
		this.getselfInfo()
		// 获取game tag
		uni.getStorage({
			key: 'games',
			success: (res) => {
				this.ourGames = JSON.parse(res.data)
			}
		})
		// 获取我的tag
		uni.getStorage({
			key: 'ourTags',
			success: (res) => {
				this.ourTags = JSON.parse(res.data)
			}
		})
		// 获取 sports tag
		uni.getStorage({
			key: 'ourSports',
			success: (res) => {
				this.ourSports = JSON.parse(res.data)
			}
		})
		// 获取 music tag
		uni.getStorage({
			key: 'ourMusic',
			success: (res) => {
				this.ourMusic = JSON.parse(res.data)
			}
		})
		// 获取 foods tag
		uni.getStorage({
			key: 'ourFoods',
			success: (res) => {
				this.ourFoods = JSON.parse(res.data)
			}
		})
		// 获取图片1
		uni.getStorage({
			key: 'photo_one',
			success: (res) => {
				// console.log(res)
				this.imgList1.push(res.data)
			}
		})
		// 获取图片2
		uni.getStorage({
			key: 'photo_two',
			success: (res) => {
				// console.log(res)
				this.imgList2.push(res.data)
			}
		})
	},
	data() {
		return {
			imgList: [],
			// 游戏tag
			ourGames: [],
			// 我的tag
			ourTags: [],
			// 我的运动
			ourSports: [],
			// 我的食物
			ourFoods: [],
			// 我的音乐
			ourMusic: [],
			sexindex: -1,
			sexbox: ['男', '女'],
			// 出生日期
			birthday: '1970-01-01',
			modalName: null,
			basicArr: [],
			formlist: [],
			baseUrl: 'http://47.104.29.236:9999/api/v1',
			id: '',
			imgList: [],
			imgList1: ["http://hbimg.b0.upaiyun.com/9817192fbf6914a38e77e0d289253e6d2f8d88b71635-VDp6dR_fw658"],
			imgList2: ["http://hbimg.b0.upaiyun.com/9817192fbf6914a38e77e0d289253e6d2f8d88b71635-VDp6dR_fw658"],
			modalName: null
		}
	},
	methods: {
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		// 点击保存修改
		saveselfInfo() {
			let date = parseFloat(new Date(this.formlist.birthday).getTime() / 1000)
			this.formlist.birthday = date
			uni.request({
				url: this.baseUrl + `/update_info/${this.id}`,
				method: 'PUT',
				data: {
					info: JSON.stringify(this.formlist)
				},
				success: (res) => {
					console.log(res)
					if (res.data.ok == 1) {
						uni.showToast({
							title: "修改成功",
						})
						this.modalName = null
					} else {
						uni.hideToast({
							title: "修改失败",
						})
					}
				}
			})
		},
		// 获取个人用户信息
		getselfInfo() {
			let userId = uni.getStorageSync('user_id')
			this.id = userId
			uni.request({
				url: this.baseUrl + `/selfInfo/${userId}`,
				data: {
					id: this.id
				},
				method: 'GET',
				success: (res) => {
					this.formlist = res.data.data
					this.imgList.push(res.data.data.head_img)
					const dt = new Date(this.formlist.birthday * 1000);
					const y = dt.getFullYear();
					const m = (dt.getMonth() + 1 + "").padStart(2, "0");
					const d = (dt.getDate() + "").padStart(2, "0");
					let data = `${y}-${m}-${d}`
					this.formlist.birthday = data
					this.birthday = this.formlist.birthday
					this.sexindex = this.formlist.sex
					// 当页面加载的时候把生日传递给form表单
					if (this.formlist.birthday == null) {
						this.formlist.birthday = '1970-01-01'
					}
				}
			})
		},
		PickerChange(e) {
			this.sexindex = e.detail.value
			this.formlist.sex = this.sexindex
		},
		DateChange(e) {
			this.birthday = e.detail.value
			this.formlist.birthday = this.birthday
		},
		ChooseImage() {
			let _self = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					const tempFilePaths = res.tempFilePaths;
					const uploadTask = uni.uploadFile({
						url: 'http://47.104.29.236:9999/api/v1/upload_phone',
						filePath: tempFilePaths[0],
						name: 'file',
						formData: {
							'user': 'test'
						},
						success: (uploadFileRes) => {
							let aa = JSON.parse(uploadFileRes.data)
							this.imgList.push(aa.info)
							this.formlist.head_img = this.imgList[this.imgList.length - 1]
						}
					});
				},
				error: function(e) {
					console.log(e);
				}
			});
		},
		ChooseImage1() {
			let _self = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					const tempFilePaths = res.tempFilePaths;
					const uploadTask = uni.uploadFile({
						url: 'http://47.104.29.236:9999/api/v1/upload_phone',
						filePath: tempFilePaths[0],
						name: 'file',
						formData: {
							'user': 'test'
						},
						success: (uploadFileRes) => {
							let aa = JSON.parse(uploadFileRes.data)
							this.imgList1.push(aa.info)
							uni.setStorage({
								key: 'photo_one',
								data: aa.info
							})
						}
					});
				},
				error: function(e) {
					console.log(e);
				}
			});
		},
		ChooseImage2() {
			let _self = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					const tempFilePaths = res.tempFilePaths;
					const uploadTask = uni.uploadFile({
						url: 'http://47.104.29.236:9999/api/v1/upload_phone',
						filePath: tempFilePaths[0],
						name: 'file',
						formData: {
							'user': 'test'
						},
						success: (uploadFileRes) => {
							let aa = JSON.parse(uploadFileRes.data)
							this.imgList2.push(aa.info)
							uni.setStorage({
								key: 'photo_two',
								data: aa.info
							})
						}
					});
				},
				error: function(e) {
					console.log(e);
				}
			});
		},
		// 退出登录
		layout() {
			uni.showLoading({
				title: 'loading'
			});
			uni.clearStorage()
			uni.navigateTo({
				url: '../login/login'
			})
		},
		// 点击跳转到tag页面
		our_sports() {
			uni.navigateTo({
				url: '../self_tag_sports/self_tag_sports'
			})
		},
		our_tags() {
			uni.navigateTo({
				url: '../self_tag_our/self_tag_our'
			})
		},
		our_games() {
			uni.navigateTo({
				url: '../self_tag_games/self_tag_games'
			})
		},
		our_music() {
			uni.navigateTo({
				url: '../self_tag_music/self_tag_music'
			})
		},
		our_foods() {
			uni.navigateTo({
				url: '../self_tag_foods/self_tag_foods'
			})
		},
	}
}
